import React from "react";
import ReactDOM from "react-dom";
import { HashRouter } from "react-router-dom";
import App from "./App";

window.renderUserService = (containerId, history) => {
  ReactDOM.render(
    <HashRouter>
      <App history={history} />
    </HashRouter>,
    document.getElementById(containerId),
  );
};

window.unmountUserService = containerId => {
  // ReactDOM.unmountComponentAtNode(document.getElementById(containerId));
};

if (!document.getElementById('UserService-container')) {
  ReactDOM.render(
    <HashRouter>
      <App />
    </HashRouter>,
  document.getElementById('root'));
}